body, header, nav, main, footer, section, div, ul,li,p,aside, article, address, h1,h2,h3,h4,h5,h6, form, fieldset, legend, table, tbody, th,td, ol, dl, dt,dd,input, button, select, option, textarea, figure, figcaption, caption, blockquote{ margin: 0; padding: 0;}
a{text-decoration: none;}
ol, ul{list-style: none;}
img{display: block; border: 0px;}
address, em, i{font-style: normal;}
button, fieldset, select, input{border: 0px;}
legend, caption, hr{display: none;}
table{border-collapse: collapse; border-spacing: 0px;}
/*기본 셋업  */

@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic+Coding:wght@400;700&family=Noto+Sans+KR:wght@100;200;300;400;500;600;700;800;900&display=swap');
/* 메인 컬러 변수 사용 */
:root {
  --main-color: #FFF3DC; /* 변수를 정의할 때 --로 시작한 다음 색상 값을 지정합니다. */
  --sub-color:#795546;
  --sub1-color:#F0DAB5;


}
body{
  font-size: 14px;
  font-family: 'Nanum Gothic Coding', Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 300;
}

/* 얘는 왜 개별지정해야 작동할까? */
textarea {
  font-size: 14px;
  font-family: 'Nanum Gothic Coding',Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 300; 
}



h1{color: #fff; margin: 0; padding:40px 0 21px 0; background:var(--sub-color) ;}
/* fcf7ee */
.phone{
  width: 360px;
  height: 740px;
  /* border: 1px solid #acd; */
  margin:  auto;
  position: relative;
  overflow: hidden;
  background: var(--main-color);
}
.wrap_table{
  width: 100%;
  max-height: 640px;
  overflow: auto;
}
.wrap_table::-webkit-scrollbar {
  width: 0.1rem; /* 수평 스크롤바 너비를 0으로 설정하여 숨깁니다. */
}

table{
  width: 94%;
  margin: auto;
  max-height: 200px;
  /* text-align: center; */
  border: none;
  color: #5C5B40;
  table-layout: fixed; 
  /* 하위요소들 width 주기 위해  */

}
tbody{
  width: 100%;
  max-height: 200px;
  border: none;
  border-collapse: collapse;
}
tr{
  width: 100%;
  /* height: 60px; */
  background:var(--main-color);
  border: none;
  border-collapse: collapse;
  border-bottom: 1px solid #dedede;
} 
td{
  padding: 15px 0px;
  width: 20px;
  font-size: 16px;
}
.tdTitle{
  padding: 15px 10px;
  width: 200px;
  height: 30px;
  white-space: nowrap; /* 텍스트를 한 줄에 표시 */
  overflow: hidden; /* 넘치는 부분을 숨김 */
  text-overflow: ellipsis; /* 생략 부호(...) 표시 */

}

.write{width: 30px;height: 30px;color: #fff;
  position: absolute; bottom:30px; right: 30px; 
  /* border: 1px solid #8c8b40; */
  border-radius: 50%;
  /* background: #FF4181; */
  background: #cc1e48;
  box-shadow: 3px 3px 4px rgba(0, 0, 0, 0.4);
  padding: 10px 10px 15px 15px;
}
.trash{
  width: 15px; height:15px;  
}

.Bu{width: 50px; height: 30px;
  background-color: var(--sub1-color); border: none;
}


.back{ padding: 7px 0 0 10px; width: 20px; height: 20px; }
.iconB{width: 20px; height: 20px;}
.writeDiv{
  display: flex; 
  background: var(--sub1-color);
  justify-content: space-between;
  border-bottom: 1px solid #888;
}



.titleV{
  width: 350px;
  margin: 0 5px;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  border: none;
  border-bottom: 2px solid #999;
  background: var(--main-color);
  resize: none;
}
.contentV{
  background: var(--main-color);
  margin: 10px 5px 0 5px ;
  width: 352px;
  height: 450px;

  border: none;
  resize: none;
}


.overT{  white-space: nowrap; /* 텍스트를 한 줄에 표시 */
  overflow: hidden; /* 넘치는 부분을 숨김 */
  text-overflow: ellipsis; /* 생략 부호(...) 표시 */
}

